<template>
  <div class="row" id="translateForm">
    <div  class="col-md-4 offset-4">
      <form id="transForm" class="well form-inline" v-on:submit = "formSubmit">
        <input class="well form-control" type="text" placeholder="输入需要翻译de内容" v-model="textToTranslate">
        <select class="form-control" v-model="language">
          <option value="en">English</option>
          <option value="ru">Russian</option>
          <option value="ko">Korean</option>
          <option value="ja">Janpenese</option>
        </select>
        <input class=" btn btn-primary" type="submit" value="翻译">
      </form>
    </div>
  </div>
</template>

<script>
    export default {
        name: 'translateForm',
        data : function(){
          return {
              textToTranslate:"",
              language:""
          }
        },
        methods:{
            formSubmit : function (e){
                // alert (this.textToTranslate);
                this.$emit("formSubmit",this.textToTranslate,this.language);    //vue 事件注册，默认一个参数
                e.preventDefault(); // 取消默认事件,让页面不刷新
            }
        },
        created:function(){
            this.language = "en";
        }
    }
</script>

<style>
#transForm{
  border-radius:10px;
  border:1px solid #ccc;
}
</style>



